import { Component, OnInit, Input, EventEmitter, Output, DoCheck, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-inline-input',
  templateUrl: './inline-input.component.html',
  styleUrls: ['./inline-input.component.css']
})


export class InlineInputComponent implements OnInit {
  edit:boolean=false;
  
  @Input('text') text:string;
  @Input('parent') parent:any;
  @Output() textChange:EventEmitter<string>=new EventEmitter<string>();
  @ViewChild('in') input:ElementRef;
  constructor() { 

  }

  ngOnInit() {
    
  }
  change(){
    this.textChange.emit(this.text);
    if(this.parent!==undefined){
      this.parent.changed=true;
    }
  }
  blurInput(){
    this.edit=false;
    // this.change();
  }
  editing(){
    this.edit=true;
    setTimeout(()=>{
      this.input.nativeElement.focus();
    },10)
    

  }

}
