
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

// Angular base modules
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';

// Angular Material Modules, Check version before adding (7.1.0/7.2.0)
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatMenuModule } from '@angular/material/menu';
import { MatButtonModule } from '@angular/material/button'; 
import { MatTabsModule } from '@angular/material/tabs';
import { MatTableModule } from '@angular/material/table';
import { MatSelectModule } from '@angular/material/select';
import { MatTreeModule } from '@angular/material/tree';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';
import { MatRadioModule } from '@angular/material/radio';
import { MatInputModule } from '@angular/material/input';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatCardModule } from '@angular/material/card';
import { MatChipsModule } from '@angular/material/chips';
import { MatListModule } from '@angular/material/list';
import { MatIconModule } from '@angular/material/icon';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatDialogModule } from '@angular/material/dialog';
import { MatSortModule } from '@angular/material';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import {
  MatFormFieldModule,
  MatDatepickerModule,
  MatNativeDateModule,
  MatGridListModule,
} from '@angular/material';
import {MatStepperModule} from '@angular/material/stepper';

// cdk modules
import { LayoutModule } from '@angular/cdk/layout';
import { ScrollDispatchModule, ScrollingModule } from '@angular/cdk/scrolling';
import {DragDropModule} from '@angular/cdk/drag-drop';

//angular calendar Modules
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';

// 3rd party modules
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { ChartModule,HIGHCHARTS_MODULES  } from 'angular-highcharts';
import * as more from 'highcharts/highcharts-more.src';
import * as solidgauge from 'highcharts/modules/solid-gauge.src';
import * as funnel from 'highcharts/modules/funnel.src';
import * as drilldown from 'highcharts/modules/drilldown.src';
// import * as Exporting from 'highcharts/modules/exporting.src';
import { QuillModule } from 'ngx-quill';
import { NgCircleProgressModule } from 'ng-circle-progress';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';

// shared components
import { HeaderComponent } from './header/header.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { CompletionChartComponent } from './completion-chart/completion-chart.component';
import { InfiniteScrollComponent } from './infinite-scroll/infinite-scroll.component';
import { MultiCheckboxComponent } from './multi-checkbox/multi-checkbox.component';
import { MultiSelectComponent } from './multi-select/multi-select.component';
import { InlineInputComponent } from './inline-input/inline-input.component';
import { TabsComponent } from './tabs/tabs.component';
import { LoadingComponent } from './loading/loading.component';



// Directives
import { ScrollDirective } from './infinite-scroll/scroll-directive';
import { ArrayPipe } from '../pipe/array.pipe';
import { SafeUrlPipe } from '../pipe/safeUrl.pipe';
import { SafePipe } from '../pipe/safe.pipe';
import { HtmlPipe } from '../pipe/html.pipe';
import { Keyobject } from '../pipe/keyobject.pipe';


//Icon
import { IconActivityComponent } from './sidebar/icon/icon-activity.component';
import { IconAppointmentsComponent } from './sidebar/icon/icon-appointments.component';
