import { IconReportsComponent } from './sidebar/icon/icon-reports.component';
import { IconStatisticsComponent } from './sidebar/icon/icon-statistics.component';
import { IconStorageComponent } from './sidebar/icon/icon-storage.component';

//angular cdk
import { OverlayModule } from '@angular/cdk/overlay';
import {CdkTableModule} from '@angular/cdk/table';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    HttpClientModule,

    MatSidenavModule,
    MatMenuModule,
    MatButtonModule,
    MatTabsModule,
    MatTableModule,
    MatTableModule,
    MatSelectModule,
    MatTreeModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatInputModule,
    MatCheckboxModule,
    MatCardModule,
    MatChipsModule,
    MatListModule,
    MatIconModule,
    MatSnackBarModule,
    MatExpansionModule,
    MatButtonToggleModule,
    MatTooltipModule,
    MatDialogModule,
    MatTableModule,
    MatSortModule,
    MatAutocompleteModule,
    MatFormFieldModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatGridListModule,
    MatAutocompleteModule,
    MatStepperModule,

    LayoutModule,
    ScrollingModule,
    ScrollDispatchModule,
    DragDropModule,

    NgxChartsModule,
    ChartModule,
    LeafletModule.forRoot(),

    QuillModule.forRoot({
      modules: {
        // toolbar: [
        //   ['bold', 'italic', 'underline', 'strike'], // toggled buttons
        //   ['blockquote', 'code-block'],
        //   [{ header: 1 }, { header: 2 }], // custom button values
        //   [{ list: 'ordered' }, { list: 'bullet' }],
        //   [{ script: 'sub' }, { script: 'super' }], // superscript/subscript
        //   [{ indent: '-1' }, { indent: '+1' }], // outdent/indent
        //   [{ direction: 'rtl' }], // text direction
        //   [{ size: ['small', false, 'large', 'huge'] }], // custom dropdown
        //   [{ header: [1, 2, 3, 4, 5, 6, false] }],
        //   [{ color: [] }, { background: [] }], // dropdown with defaults from theme
        //   [{ font: [] }],
        //   [{ align: [] }],
        //   ['clean'], // remove formatting button
        //   ['link', 'image', 'video'] // link and image, video
        // ]
        toolbar: [ 
          [{ 'font': [] }],
          [{ 'size': [] }],  // custom dropdown
          ['bold', 'italic', 'underline'],        // toggled buttons
          [{ 'color': [] }],          // dropdown with defaults from theme
          [{ 'align': [] }],
          [{ 'list': 'ordered' }, { 'list': 'bullet' }],
          [{ 'indent': '-1' }, { 'indent': '+1' }],          // outdent/indent
          ['blockquote'],
          ['clean'],                                         // remove formatting button
        ]
      }
    }),

    CalendarModule.forRoot({
      provide: DateAdapter,
      useFactory: adapterFactory
    }),

    NgCircleProgressModule.forRoot({
      // set defaults here
      space: -6,
      outerStrokeWidth: 10,
      titleColor: '#333',
      unitsColor: '#333',
      subtitleColor:'#333',
      titleFontSize: '24',
      unitsFontSize: '14',
      innerStrokeWidth: 6,
      animateTitle: true,
      showTitle: false,
      showUnits: false,
      showBackground: false,
      clockwise: true
    })


  ],
  declarations: [
    HeaderComponent,
    SidebarComponent,
    CompletionChartComponent,
    InfiniteScrollComponent,
    MultiCheckboxComponent,
    MultiSelectComponent,
    InlineInputComponent,

    ArrayPipe,
    SafeUrlPipe,
    HtmlPipe,
    SafePipe,
    Keyobject,
    
    IconActivityComponent,
    IconAppointmentsComponent,
    IconAssignmentsComponent,
    IconCalendarComponent,
    IconDashboardComponent,
    IconHomeComponent,
    IconInvoicingComponent,
    IconMessagesComponent,
    IconNotesComponent,
    IconReportsComponent,
    IconStatisticsComponent,
    IconStorageComponent,
    ScrollDirective,
    MultiSelectComponent,
    TabsComponent,
    LoadingComponent
  ],
  exports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,

    MatSidenavModule,
    MatMenuModule,
    MatButtonModule,
    MatTabsModule,
    MatTableModule,
    MatSelectModule,
    MatTreeModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatInputModule,
    MatCheckboxModule,
    MatCardModule,
    MatChipsModule,
    MatListModule,
    MatIconModule,
    MatSnackBarModule,
    MatExpansionModule,
    MatButtonToggleModule,
    MatTooltipModule,
    MatDialogModule,
    MatTableModule,
    MatSortModule,
    MatAutocompleteModule,
    MatFormFieldModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatGridListModule,
    MatFormFieldModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatGridListModule,
    MatAutocompleteModule,
    MatStepperModule,

    LayoutModule,
    ScrollingModule,
    ScrollDispatchModule,
    DragDropModule,

    IconActivityComponent,
    IconAppointmentsComponent,
    IconAssignmentsComponent,
    IconCalendarComponent,
    IconDashboardComponent,
    IconHomeComponent,
    IconInvoicingComponent,
    IconMessagesComponent,
    IconNotesComponent,
    IconReportsComponent,
    IconStatisticsComponent,
    IconStorageComponent,
    
    NgxChartsModule,
    ChartModule,
    QuillModule,
    CalendarModule,
    NgCircleProgressModule,
    
    HeaderComponent,
    SidebarComponent,
    CompletionChartComponent,
    InfiniteScrollComponent,
    MultiCheckboxComponent,
    MultiSelectComponent,
    InlineInputComponent,
    TabsComponent,
    LoadingComponent,

    ArrayPipe,
    SafeUrlPipe,
    HtmlPipe,
    SafePipe,
    Keyobject,

    ScrollDirective,
    OverlayModule,
    CdkTableModule
  ],
  providers: [
    { provide: HIGHCHARTS_MODULES, useFactory: () => [ more,solidgauge,funnel,drilldown] } // add as factory to your providers
  ]
})
export class SharedModule {}
