import { Injectable, EventEmitter } from '@angular/core';
import { FactoryService } from './factory.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {MediaMatcher} from '@angular/cdk/layout';
@Injectable({
  providedIn: 'root'
})
export class AuthService extends FactoryService {
  public header = true;
  public sidebar = true;
  public getUserinfo_flag = new EventEmitter<any>();
  public userinfo: any = { login_status: false };
  mobileQuery: MediaQueryList;

  constructor(protected http: HttpClient, private media: MediaMatcher) {
    super(http);
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
  }
  
  //检查是否有注册用户名
  public api_get_username(ID){
    return new Promise<any>(resolve => {
      this.get('/members/passport/get_username/?ID='+ID).then(res => {
        resolve(res);
      });
    });
  }

  // 用戶登錄
  public api_login(username: string, password: string) {
    username = encodeURIComponent(username);
    password = encodeURIComponent(password);
    return new Promise<any>(resolve => {
      this.get(
        '/members/passport/login/?username=' +
          username +
          '&password=' +
          password
      ).then(res => {
        this.userinfo=res;
        resolve(res);
      });
    });
  }

  // 獲取用戶當前session
  public refresh_userinfo(){
    this.api_login_status().then(res => {
      this.userinfo = res;
    });
  }
  public get_userinfo() {
    this.api_login_status().then(res => {
      this.userinfo = res;
      this.getUserinfo_flag.emit(res);
    });
  }
  public api_login_status() {
    return new Promise<any>(resolve => {
      this.get('/members/passport/login_status/').then(res => {
        resolve(res);
      });
    });
  }
  
  //退出登陆
  public api_logout(){
    return new Promise<any>(resolve => {
      this.get('/members/passport/logout/').then(res => {
        resolve(res);
      });
    });
  }
  
  //选择学生
  public api_select_student(studentID){
    return new Promise<any>(resolve => {
      this.get('/members/passport/select_student/?studentID='+studentID).then(res => {
        resolve(res);
      });
    });
  }
  
  //退回advisor
  public api_unselect_student(){
    return new Promise<any>(resolve => {
      this.get('/members/passport/unselect_student/').then(res => {
        resolve(res);
      });
    });
  }

  public getStudentID():number{
    if(this.userinfo.login_status!==true) return 0;
    if(this.userinfo.login_user.RoleID!==7) {
      if(this.userinfo.login_user.student==undefined) return 0;
      else return this.userinfo.login_user.student.id;
    }
    else return this.userinfo.login_user.id;
  }
  
  public getConfig(field:string=null){
    if(field!== null){
      if(this.userinfo['config'][field]){
        return this.userinfo['config'][field];
      }
      else return [];
    }
    return this.userinfo['config'];
  }
  //检查username
  public api_check_username(username){
    return new Promise<any>(resolve => {
      this.get('/members/passport/check_register_username/?username='+username).then(res => {
        resolve(res);
      });
    });
  }
  
  //检查email
  public api_check_email(email){
    return new Promise<any>(resolve => {
      this.get('/members/passport/check_register_email/?email='+email).then(res => {
        resolve(res);
      });
    });
  }
  
  //检查password
  public api_check_password(password){
    return new Promise<any>(resolve => {
      this.get('/members/passport/check_register_password/?password='+password).then(res => {
        resolve(res);
      });
    });
  }
  
  //检查password_confirm
  public api_check_password_confirm(password,password_confirm){
    return new Promise<any>(resolve => {
      this.get('/members/passport/check_register_password_confirm/?password='+password+'&password_confirm='+password_confirm).then(res => {
        resolve(res);
      });
    });
  }
  
  //注册
  public api_register(data){
    return new Promise<any>(resolve => {
      this.post('/members/passport/register/',data).then(res => {
        resolve(res);
      });
    });
  }
  
  //修改密码
  public api_changePassword(data){
    return new Promise<any>(resolve => {
      this.post('/members/passport/reset_password/',data).then(res => {
        resolve(res);
      });
    });
  }
  
  //机构注册
  public api_federation_register(data){
    return new Promise<any>(resolve => {
      this.post('/members/passport/federation_register/',data).then(res => {
        resolve(res);
      });
    });    
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
