import { Component, OnInit,OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  public post_data;
  public step;
  
  public ID;
  public RegistrationKey;
  
  public form_check;
  public success;
  
  constructor(public auth: AuthService, private router: Router,public route: ActivatedRoute) { 
    this.step=1;
    this.post_data={
      "StateOrProvinceID":1006,
      "CountryID":1241,
      "SMSGatewayID":0,
      "TimeZoneID":'Pacific Standard Time',
    };
    
    this.form_check={
      "username":{"init":true,'status':false,'lock':"open",'disable':false},
      "email":{"init":true,'status':false,'lock':"open"},
      "password":{"init":true,'status':false,'lock':"open"},
      "password_confirm":{"init":true,'status':false,'lock':"open"},
    };
    
    this.check_form();
    this.success=false;
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.ID = +params['ID'];
      this.RegistrationKey = +params['RegistrationKey'];
      
      this.get_username(this.ID);
      this.post_data['ID']=this.ID;
      this.post_data['RegistrationKey']=this.RegistrationKey;
      
    });
  }
  
  previous(){
    this.step-=1;
  }
  
  next(){
    this.step+=1;
  }
  
  get_username(ID){
    this.auth.api_get_username(ID).then(res => {
      if(res.UserName!=null){
        this.post_data.username=res.UserName;
        this.post_data.new_customer=true;
        this.form_check.username={"init":false,'status':true,'disable':true};
      }
    });
  }

  check_username(){
    setTimeout(()=>{    
      if(this.form_check.username.lock=="open"){
        this.auth.api_check_username(encodeURIComponent(this.post_data.username)).then(res => {
          this.form_check.username=res;
          this.form_check.username.lock="open";
          this.check_form();
        });
      }
      
      this.form_check.username.lock="close";
    }, 1000);
  }
  
  check_email(){
    setTimeout(()=>{    
      if(this.form_check.email.lock=="open"){
        this.auth.api_check_email(encodeURIComponent(this.post_data.email)).then(res => {
          this.form_check.email=res;
          this.form_check.email.lock="open";
          this.check_form();
        });
      }
      
      this.form_check.email.lock="close";
    }, 1000);
  }
  
  check_password(){
    setTimeout(()=>{    
      if(this.form_check.password.lock=="open"){
        this.auth.api_check_password(encodeURIComponent(this.post_data.password)).then(res => {
          this.form_check.password=res;
          this.form_check.password.lock="open";
          this.check_form();
          
          if(this.post_data.password_confirm!=""){
            this.check_password_confirm();
          }
          
        });
      }
      
      this.form_check.password.lock="close";
    }, 1000);
  }
  
  check_password_confirm(){
    setTimeout(()=>{    
      if(this.form_check.password_confirm.lock=="open"){
        this.auth.api_check_password_confirm(encodeURIComponent(this.post_data.password),encodeURIComponent(this.post_data.password_confirm)).then(res => {
          this.form_check.password_confirm=res;
          this.form_check.password_confirm.lock="open";
          this.check_form();
        });
      }
      
      this.form_check.password_confirm.lock="close";
    }, 1000);
  }
  
  check_form(){
    for(let v in this.form_check){
      if(!this.form_check[v].status){
        this.success=false;
        return;
      }  
    }  
    
    this.success=true;
  }
  
  register(){
    this.auth.api_register({"data":this.post_data}).then(res => {
      this.router.navigateByUrl("/account/logon");
    });
  }
  
  
  
  
  
  
  
  
  
}




























