import { Component, OnInit,OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-consultants-v2',
  templateUrl: './consultants-v2.component.html',
  styleUrls: ['./consultants-v2.component.css']
})
export class ConsultantsV2Component implements OnInit {

  public post_data;
  
  public form_check;
  public success;
  
  constructor(public auth: AuthService, private router: Router,public route: ActivatedRoute) { 
  
    this.post_data={
      "term":false,
      "federationID":4434 
    };
    
    this.form_check={
      "Firstname":{"init":true,'status':false,'lock':"open"},
      "Lastname":{"init":true,'status':false,'lock':"open"},
      "federation":{"init":true,'status':false,'lock':"open"},
      "UserName":{"init":true,'status':false,'lock':"open"},
      "Email":{"init":true,'status':false,'lock':"open"},
    };
    
    this.check_form();
    this.success=false;
  }

  ngOnInit() {
    
  }
  
  check_UserName(){
    setTimeout(()=>{    
      if(this.form_check.UserName.lock=="open"){
        this.auth.api_check_username(encodeURIComponent(this.post_data.UserName)).then(res => {
          this.form_check.UserName=res;
          this.form_check.UserName.lock="open";
          this.check_form();
        });
      }
      
      this.form_check.UserName.lock="close";
    }, 1000);
  }
  
  check_Email(){
    setTimeout(()=>{    
      if(this.form_check.Email.lock=="open"){
        this.auth.api_check_email(encodeURIComponent(this.post_data.Email)).then(res => {
          this.form_check.Email=res;
          this.form_check.Email.lock="open";
          this.check_form();
        });
      }
      
      this.form_check.Email.lock="close";
    }, 1000);
  }
  
  check_Firstname(){
    if(this.post_data.Firstname=="" || this.post_data.Firstname==undefined){
      this.form_check.Firstname={"status":false,"init":false,"msg":"First name required"};
    }else{
      this.form_check.Firstname={"status":true,"init":false};
    }
    this.check_form();
  }
  
  check_Lastname(){
    if(this.post_data.Lastname=="" || this.post_data.Lastname==undefined){
      this.form_check.Lastname={"status":false,"init":false,"msg":"Last name required"};
    }else{
      this.form_check.Lastname={"status":true,"init":false};
    } 
    this.check_form();
  }
  
  check_federation(){
    if(this.post_data.federation=="" || this.post_data.federation==undefined){
      this.form_check.federation={"status":false,"init":false,"msg":"Organization name required"};
    }else{
      this.form_check.federation={"status":true,"init":false};
    }
    this.check_form();
  }
  
  check_form(){
    for(let v in this.form_check){
      if(!this.form_check[v].status){
        this.success=false;
        return;
      }  
    }  
    
    this.success=true;
  }
  
  register(){    
    this.auth.api_federation_register({"data":this.post_data}).then(res => {
      alert("A Customer Success notification has been sent to your email address: "+this.post_data.Email);
      this.router.navigateByUrl("/account/logon");
    });
  }

  
  
  
  
}























