import { Injectable, EventEmitter } from '@angular/core';
import { FactoryService } from './factory.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CollegeService extends FactoryService {
  chart:any={
    maincolors:['#97c846','#105f95'],
    colors:['#65b7ec','#1370ef', '#8f3faf', '#00c3e1', '#f2c300', '#83c971'],
    credit:{
      enabled:true,
      href:'https://www.guidedpath.net',
      position:{align:'right',
        verticalAlign:'bottom',
        x:-10,
        y:-5},
      style:{"cursor": "pointer", "color": "#999999", "fontSize": "10px"},
      text:'GuidedPath.net',
    },
  }

// dev-data
dashbooard:any={
  myCollegeList:{
    title:'My College List',
    url:"/",
    series:{
      data:[
        ['My colleges', 7],
        ['Interested Colleges', 13]
      ]
    }
  },
  myChances:{
    title:'My Chances',
    url:"/",
    subTitle:'Admission Expectations',
    series:[{
      name: 'My Colleges',
      data: [5, 3, 4, 7, 2]
      }, {
      name: 'Interested Colleges',
      data: [2, 2, 3, 2, 1]
    }],
    categories: ['Likely', 'Target', 'Reach', 'Wildcard', 'Unknown'],
    yaxisTitle:'Number of Colleges',
  },
  myCollegeUndergraduateSize:{
    title:"My College Undergraduate Size",
    subTitle:"DISTRIBUTION",
    series:{
      data:[
        ['More then 20,000', 5],
        ['10,000-19,999', 13],
        ['5,000-9,999', 13],
        ['2,000-4,999', 3],
        ['Less than 2,000', 3],
        ['Unknown', 3]
      ]
    }
  },
  interestedCollegeUndergraduateSize:{
    title:"Interested College Undergraduate Size",
    subTitle:"DISTRIBUTION",
    series:{
      data:[
        ['More then 20,000', 5],
        ['10,000-19,999', 13],
        ['5,000-9,999', 13],
        ['2,000-4,999', 3],
        ['Less than 2,000', 3],
        ['Unknown', 3]
      ]
    }
  },
  netPriceRatings:{
    title:'Net Price Estimate College Distribution Ratings',
    subTitle:'Based on Average Percent Need Met and Number of Students with Need Fully Met',
    url:"/",
    series:[{
      name: 'My Colleges',
      data: [5, 3, 1, 4, 4, 2]
      }, {
      name: 'Interested Colleges',
      data: [2, 0, 3, 2, 1, 0]
    }],
    categories: ['none','1 ★', '2 ★', '3 ★', '4 ★', '5 ★'],
    yaxisTitle:'Number of Colleges',
  },
};

//end of dev-data
  constructor(protected http: HttpClient) {
    super(http);
  }
  // DashBoard
  getDashboard(){
    return new Promise<any>(resolve => {
      this.get('/college/collegedashboard/getData/').then(res => {
        resolve(res);
      });
    });
  }
  // Guided Search - my matches
  getMyMatches(){
    return new Promise<any>(resolve => {
      this.get('/college/searchcollege/initMyMatches/').then(res => {
        resolve(res);
      });
    });
  }

  // Save Tagged List
  saveTaggedList(query:any){
    return new Promise<any>(resolve=>{
      this.post('/college/searchcollege/saveTaggedList/',query).then(res=>{
        resolve(res);
      })
    })
  }
  // Save Current search
  saveCurrentSearch(query:any){
    return new Promise<any>(resolve=>{
      this.post('/college/searchcollege/saveCurrentSearch/',query).then(res=>{
        resolve(res);
      })
    })
  }
  // Edit current search
  editCurrentSearch(id,query){
    return new Promise<any>(resolve=>{
      this.post('/college/searchcollege/editCurrentSearch/?id='+id,query).then(res=>{
        resolve(res);
      })
    })
  }
  // Delete current search
  deleteCurrentSearch(id){
    return new Promise<any>(resolve=>{
      this.get('/college/searchcollege/deleteCurrentSearch/?id='+id).then(res=>{
        resolve(res);
      })
    })
  }
  // Get current search list
  getCurrentSearch(){
    return new Promise<any>(resolve => {
      this.get('/college/searchcollege/getCurrentSearch/').then(res => {
        resolve(res);
      });
    });
  }
  
  //全站搜索大学
  searchCollegeList(word:string){
    return new Promise<any>(resolve => {
      this.get('/college/searchcollege/suggestWord/?word='+word).then(res => {
        resolve(res);
      });
    });
  }
  
  //搜索单一学校信息
  searchCollegeInfo(schoolname:string){
    return new Promise<any>(resolve => {
      this.get('/college/searchcollege/find/?schoolname='+schoolname).then(res => {
        resolve(res);
      });
    });
  }
  removeDiscuss(id){
    return new Promise<any>(resolve => {
      this.get('/college/collegeprofile/removeDiscuss/?id='+id).then(res => {
        resolve(res);
      });
    });
  }
  addDiscuss(programID,msg){
    let post={msg:msg}
    return new Promise<any>(resolve => {
      this.post('/college/collegeprofile/addDiscuss/?programID='+programID,post).then(res => {
        resolve(res);
      });
    });
  }
  saveNote(noteID,programID,type,note){
    let post={note:note}
    return new Promise<any>(resolve => {
      this.post('/college/collegeprofile/saveNote/?noteID='+noteID+'&programID='+programID+'&type='+type,post).then(res => {
        resolve(res);
      });
    });
    
  }
  getCollegeList(){
    return new Promise<any>(resolve => {
      this.get(
        '/college/listsummary/getCollegeList/'
      ).then(res => {
        resolve(res);
      });
    });
  }
  // 
  collegeListMoveCollege( id:string, toTypeID, toRank){
    return new Promise<any>(resolve => {
      this.get(
        '/college/listsummary/moveCollege/?id=' + id + '&toTypeID=' + toTypeID + '&toRank=' + toRank
      ).then(res => {
        resolve(res);
      });
    });
  }
  // add college to my list
  addToMyList(type,id){
    let post={programID:id,typeID:type}
    return new Promise<any>(resolve=>{
      this.post('/college/listsummary/addToCollegeList/',post).then(res=>{
        resolve(res);
      })
    })
  }
  removeFormMyCollegeList(id){
    return new Promise<any>(resolve => {
      this.get('/college/listsummary/removeCollege/?id='+id).then(res=>{
        resolve(res);
      })
    })
  }

  public getSchoolNameList(name: any) {
    return new Promise<any>(resolve => {
      this.get('/college/searchcollege/suggestWord/?word=' + name).then(data => {
        resolve(data.data);
      })
    })
  }
  public getFlterInfo(which:any){
    return new Promise<any>(resolve=>{
      this.get('/college/searchcollege/get'+which).then(data =>{
        resolve(data);
      })
    })
  }
  public getSearchResult(options:any){
    return new Promise<any>(resolve=>{
      this.get('/college/searchcollege/find/?'+options).then(data =>{
        resolve(data);
      })
    })
  }
  public getCollegeDetail(id:any,param:any){
    return new Promise<any>(resolve=>{
      this.get('/college/collegeprofile/getProfile/?id='+id+'&module='+param).then(data =>{
        resolve(data);
      })
    })
  }
  getCollegeMapList(){
    return new Promise<any>(resolve => {
      this.get('/college/collegemap/getList/').then(res => {
        resolve(res);
      });
    });
  }
  getMyChancesList(){
    return new Promise<any>(resolve => {
      this.get('/college/mychances/getList/').then(res => {
        resolve(res);
      });
    });
  }
  getCompareList(){
    return new Promise<any>(resolve => {
      this.get('/college/costcompare/getCompare/').then(res => {
        resolve(res);
      });
    });
  }
  editCollege(id,post:any){
    return new Promise<any>(resolve=>{
      this.post('/college/listsummary/editCollege/?programID='+id,post).then(res=>{
        resolve(res);
      })
    })
  }
}
