import { ScrollDirective } from './scroll-directive';
import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ContentChild,
  TemplateRef,
  DoCheck,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  AfterViewInit
} from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { Observable, BehaviorSubject, Subject } from 'rxjs';
import { map, mergeMap, tap } from 'rxjs/operators';

@Component({
  selector: 'app-infinite-scroll',
  templateUrl: './infinite-scroll.component.html',
  styleUrls: ['./infinite-scroll.component.css']
})
export class InfiniteScrollComponent implements OnInit, DoCheck, AfterViewInit {
  @ViewChild('scroll') scroll: CdkVirtualScrollViewport;
  @ContentChild(ScrollDirective, { read: TemplateRef }) scrollItemTemplate;
  @Input()
  items = [];
  @Input()
  size = 100;
  back_items = [];
  show_items = [];
  back_length = -1;
  @Output() update = new EventEmitter<any>();
  offset = new BehaviorSubject(null);
  subject = new Subject<any[]>();

  ended = false;

  map = {};

  constructor(private cd: ChangeDetectorRef) {
    this.back_items = this.items;
  }

  ngOnInit() {
    this.show_items = [...this.items];
  }

  public checkStatus(event) {
    if (this.ended) {
      return;
    }
    const total = this.scroll.getDataLength();
    const end = this.scroll.getRenderedRange().end;
    if (end / total > 0.9) {
      if (this.map[total] === undefined) {
        this.map[total] = true;
        this.update.emit(true);
      }
    }
  }
  ngDoCheck() {
    if (this.back_items !== this.items) {
      this.back_items = this.items;
      this.reset();
      this.subject.next(this.items);
    } else if (this.back_length !== this.items.length) {
      this.back_length = this.items.length;
      console.log('change?');
      this.subject.next(this.items);
    }
    this.show_items = JSON.parse(JSON.stringify(this.items));
    this.cd.detectChanges();
  }
  ngAfterViewInit() {
  }
  private reset() {
    this.ended = false;
    this.map = {};
  }
}
