import { Component, OnInit, Input, EventEmitter, Output, DoCheck } from '@angular/core';
import { ChildrenOutletContexts } from '@angular/router';

@Component({
  selector: 'app-multi-select',
  templateUrl: './multi-select.component.html',
  styleUrls: ['./multi-select.component.css']
})
export class MultiSelectComponent implements OnInit, DoCheck {
  @Input() choices;
  @Input() data;
  resmap=[];
  @Output() dataChange = new EventEmitter<string>();
  constructor() { }

  ngOnInit() {
    this.resmap=JSON.parse(this.data);
  }
  ngDoCheck() {
    // this.resmap=JSON.parse(this.data);
  }
  change($event){
    console.log($event);
    this.convertData();
  }
  convertData(){
    this.data=JSON.stringify(this.resmap);
    this.dataChange.emit(this.data);
  }
}
