import { Component, OnInit,OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.css']
})
export class ChangepasswordComponent implements OnInit {
  public post_data;
  
  public ID;
  public RegistrationKey;
  
  public form_check;
  public success;
  
  constructor(public auth: AuthService, private router: Router,public route: ActivatedRoute) { 
    this.post_data={
      "password":"",
      "password_confirm":"",
    };
    
    this.form_check={
      "password":{"init":true,'status':false,'lock':"open"},
      "password_confirm":{"init":true,'status':false,'lock':"open"},
    };
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.ID = +params['ID'];
      this.RegistrationKey = +params['RegistrationKey'];
      
      this.post_data['ID']=this.ID;
      this.post_data['RegistrationKey']=this.RegistrationKey;
      
    });
  }
  
  check_password(){
    setTimeout(()=>{    
      if(this.form_check.password.lock=="open"){
        this.auth.api_check_password(encodeURIComponent(this.post_data.password)).then(res => {
          this.form_check.password=res;
          this.form_check.password.lock="open";
          this.check_form();
          
          if(this.post_data.password_confirm!=""){
            this.check_password_confirm();
          }
          
        });
      }
      
      this.form_check.password.lock="close";
    }, 1000);
  }
  
  check_password_confirm(){
    setTimeout(()=>{    
      if(this.form_check.password_confirm.lock=="open"){
        this.auth.api_check_password_confirm(encodeURIComponent(this.post_data.password),encodeURIComponent(this.post_data.password_confirm)).then(res => {
          this.form_check.password_confirm=res;
          this.form_check.password_confirm.lock="open";
          this.check_form();
        });
      }
      
      this.form_check.password_confirm.lock="close";
    }, 1000);
  }
  
  check_form(){
    for(let v in this.form_check){
      if(!this.form_check[v].status){
        this.success=false;
        return;
      }  
    }  
    
    this.success=true;
  }
  
  save(){
    this.auth.api_changePassword(this.post_data).then(res => {
      this.router.navigateByUrl("/account/logon");
    });
  }
  
  
  
  

}
