import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

export class FactoryService {
  public options: Object;

  constructor(
    protected http: HttpClient,
    protected headers:HttpHeaders=null,
    ) {

    if(headers==null){
      headers = new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'
        // application/x-www-form-urlencoded

      });
    }

    this.options = { headers: headers, responseType: 'json' };
  }
  // default ----- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- -----
  public get(query: string): Promise<any> {
    return new Promise<any>(resolve => {
      this.http.get('/api/v1' + '' + query).subscribe(data => {
        resolve(data);
      }, error => {
        console.log(error);
        resolve(null);
      });
    });
  }

  protected put(query: string, posts: any): Promise<any> {
    return new Promise<any>(resolve => {
      this.http.put('/api/v1' + query, JSON.stringify(posts), this.options).subscribe(data => {

        resolve(data);
      }, error => {
        console.log(error);
        resolve(null);
      });
    });
  }
  protected post(query: string, posts: any = null, options = null): Promise<any> {

    return new Promise<any>(resolve => {
      this.http.post('/api/v1' + query, posts, this.options).subscribe(data => {
        resolve(data);
      });
    });
  }

}
