import { Component, OnInit, Input, EventEmitter, Output, DoCheck } from '@angular/core';
import { ChildrenOutletContexts } from '@angular/router';

@Component({
  selector: 'app-multi-checkbox',
  templateUrl: './multi-checkbox.component.html',
  styleUrls: ['./multi-checkbox.component.css']
})
export class MultiCheckboxComponent implements OnInit, DoCheck {
  @Input() choices;
  @Input() data;
  resmap={};
  @Output() dataChange = new EventEmitter<string>();
  constructor() { }

  ngOnInit() {
    this.mapData();
  }
  mapData(){
    let tmp=JSON.parse(this.data);
    for(let i=0;i<tmp.length;i++){
      this.resmap[tmp[i]]=true;
    }
  }
  ngDoCheck(){
    // this.mapData();
  }
  change(choice:any, $event){
    this.resmap[choice.ID]=$event.checked;
    this.convertData();
  }
  convertData(){
    let keys=Object.keys(this.resmap);
    let tmp_data=[];
    for(let i=0;i<keys.length; i++){
      if(this.resmap[keys[i]]){
        tmp_data.push(parseInt(keys[i]));
      }
    }
    this.data=JSON.stringify(tmp_data);
    this.dataChange.emit(this.data);
  }
}
