import { Component } from '@angular/core';
import { AuthService } from './service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [AuthService]
})
export class AppComponent {
  title = 'GuidedPath';
  init = false;

  constructor(public router: Router, public auth: AuthService) {
    this.auth.get_userinfo();
    this.checkAuth();
  }

  checkAuth() {
    this.auth.getUserinfo_flag.subscribe(res => {
      if (this.router.url !== '/account/LoginHelp' &&  
          !this.router.url.includes('/account/register') &&  
          !this.router.url.includes('/account/changepassword') && 
          !this.router.url.includes('/account/ConsultantsV2') && 
          !this.router.url.includes('/account/Counselors') && 
          !this.router.url.includes('/account/NewISEdgeUser') && 
          !this.router.url.includes('/account/PARTNERS') &&
          !this.router.url.includes('/account/efconlysignup')
        ) {
        // 未登陆
        if(!this.auth.userinfo.login_status){
          this.router.navigateByUrl("/account/logon");
        }
      }
      
      this.init = true;
    });
  }
}
