import { Component, OnInit,OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit,OnDestroy {
  
  username: string = '';
  password: string = '';
  autologin: boolean = false;
  
  form_error={"login_status":false};
  constructor(public auth: AuthService, private router: Router) {
    this.auth.header=false;
    this.auth.sidebar=false;

    this.form_error['login_status']=true;
    this.check_auth();
  }

  ngOnInit() {
    
  }
  
  check_auth(){
    if(this.auth.userinfo.login_status){
      this.redirect(this.auth.userinfo.login_user.userRole);
    }
  }
  
  login(){
    this.auth.api_login(this.username,this.password).then(res => {
      if(!res.login_status){
        this.form_error=res;
      }else{
        localStorage.setItem('token', res.login_user.token);
        this.redirect(res.login_user.userRole);
      }
    });
  }
  
  redirect(userRole){
    let link='';
    
    //专家
    if(userRole=="advisor"){
      link='/Home/AdvisorHome';
    }
    
    //学生
    if(userRole=="student"){
      link='/dashboard/index';
    }
    
    //家长
    if(userRole=="connection"){
      link='/dashboard/index';
    }

    if(link!=''){
      this.router.navigateByUrl(link);
    }
  }
  
  onKeydown(event) {
    if (event.key === "Enter") {
      this.login();
    }
  }
    
  ngOnDestroy(){
    this.auth.header=true;
    this.auth.sidebar=true;
  }
  
  
  
  
  
  
  
  
  
  
  
  
}
