import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from './shared/shared.module';

import { BrowserModule, Title } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppComponent } from './app.component';
import { LoginComponent } from './public/login/login.component';
import { LoginHelpComponent } from './public/login-help/login-help.component';
import { PageNotFoundComponent } from './public/page-not-found/page-not-found.component';
import { RegisterComponent } from './public/register/register.component';
import { ChangepasswordComponent } from './public/changepassword/changepassword.component';
import { ConsultantsV2Component } from './public/consultants-v2/consultants-v2.component';
import { CounselorsComponent } from './public/counselors/counselors.component';
import { PARTNERSComponent } from './public/partners/partners.component';
import { NewISEdgeUserComponent } from './public/edge/edge.component';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    LoginHelpComponent,
    PageNotFoundComponent,
    RegisterComponent,
    ChangepasswordComponent,
    ConsultantsV2Component,
    CounselorsComponent,
    PARTNERSComponent,
    NewISEdgeUserComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule,
    
    RouterModule.forRoot([
      { path: 'account/logon', component:LoginComponent },
      { path: 'account/register/:ID/:RegistrationKey', component:RegisterComponent },
      { path: 'account/changepassword/:ID/:RegistrationKey', component:ChangepasswordComponent },
      { path: 'account/LoginHelp', component:LoginHelpComponent },
      { path: 'account/ConsultantsV2', component:ConsultantsV2Component },
      { path: 'account/Counselors', component:CounselorsComponent },
      { path: 'account/PARTNERS', component:PARTNERSComponent },
      { path: 'account/NewISEdgeUser', component:NewISEdgeUserComponent },
      {
        path: 'survey',
        loadChildren: './survey/survey.module#SurveyModule'
      },
      {
        path: 'testing',
        loadChildren: './testing/testing.module#TestingModule'
      },
      {
        path: 'college',
        loadChildren: './college/college.module#CollegeModule'
      },
      {
        path: 'application',
        loadChildren: './application/application.module#ApplicationModule'
      },
      {
        path: 'decision',
        loadChildren: './decision/decision.module#DecisionModule'
      },
      {
        path: 'account',
        loadChildren: './account/account.module#AccountModule'
      },
      { 
        path: '',
        loadChildren: './dashboard/dashboard.module#DashboardModule'
      },
      { path: '**',component: PageNotFoundComponent }
    ]
    ),
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
