import { Component, OnInit, Input, OnChanges } from '@angular/core';

@Component({
  selector: 'app-completion-chart',
  templateUrl: './completion-chart.component.html',
  styleUrls: ['./completion-chart.component.css']
})
export class CompletionChartComponent implements OnInit, OnChanges {

  @Input('data') multi = [
    {
      "name": "Surveys",
      "series": [

        {
          "name": "Not Started",
          "value": 3
        }
        ,
        {
          "name": "In-Progress",
          "value": 4
        }
        , {
          "name": "Finished",
          "value": 8
        }
      ]
    }
  ];
  tmp_multi = [];
  @Input('view') view: any[] = [1400, 130];

  // options
  @Input('showXAxis') showXAxis = true;
  @Input('showYAxis') showYAxis = false;
  @Input('gradient') gradient = false;
  @Input('showLegend') showLegend = false;
  @Input('showXAxisLabel ') showXAxisLabel = true;
  @Input('xAxisLabel') xAxisLabel = 'Number of Surveys';
  @Input('showYAxisLabel') showYAxisLabel = false;
  @Input('yAxisLabel') yAxisLabel = 'Number of Surveys';
  @Input('showDataLabel') showDataLabel = 'true';

  @Input('colorScheme') colorScheme = {
    domain: ['#C23934', '#FFB75D', '#04844B', '#00396B']
  };
  boxs = [];
  constructor() {

  }

  ngOnInit() {
    this.boxs = [];
    this.tmp_multi = JSON.parse(JSON.stringify(this.multi));
    for (let i = 0; i < this.tmp_multi[0].series.length; i++) {
      let serie = this.tmp_multi[0].series[i];
      this.boxs[serie.name] = true;
    }
  }
  ngOnChanges() {
    this.ngOnInit();
  }
  clickChart($event) {
    this.boxs[$event] = !this.boxs[$event];
    let tmp_series: any = {}, series: any = {};
    for (let i = 0; i < this.multi[0]['series'].length; i++) {
      if (this.multi[0]['series'][i].name == $event) {
        console.log($event);
        tmp_series = this.tmp_multi[0]['series'][i];
        series = this.multi[0]['series'][i];

        if (tmp_series['value'] == series['value']) {
          console.log($event);
          this.tmp_multi[0]['series'][i]['value'] = 0;
        }
        else {
          this.tmp_multi[0]['series'][i]['value'] = series['value'];
        }
        // break;
      }
    }
    this.tmp_multi = [...this.tmp_multi];
    console.log(this.tmp_multi[0]);
    console.log(this.multi[0]);
  }
}
